@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* This css file holds only the transition effects.
CSS for the images and view box can be found in ./View.css */

.fade-enter {
    opacity: 0;
}

.fade-enter-active {
    opacity: 1;
    transition: opacity 500ms linear;
    -webkit-transition: opacity 500ms linear;
    -moz-transition: opacity 500ms linear;
    -ms-transition: opacity 500ms linear;
    -o-transition: opacity 500ms linear;
}
.fade-exit {
    opacity: 1;
}

.fade-exit-done {
    opacity: 0;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 500ms linear;
    -webkit-transition: opacity 500ms linear;
    -moz-transition: opacity 500ms linear;
    -ms-transition: opacity 500ms linear;
    -o-transition: opacity 500ms linear;
}
.view-box {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 1000px;
    height: 84vh;
    /* z-index: -10; */
    margin: 0 auto;
}


.slide {
    /* opacity: 0; */
    position: absolute;
    top: auto;
    left: auto;
    right: auto;
    box-sizing: border-box;
    max-width: 100%;
    max-height: 800px;
    /* margin: auto 50%; */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
    max-height: 80vh;
    max-width: 100%;
    
}
.slide.current-slide {
    opacity: 1;
}

.slide.next-slide, .slide.hide, .slide.restart {
    opacity: 0;
}

.play-button {
    fill: aliceblue;
    display: none;
    z-index: 5;
    height: 70px;
    width: 70px;
}
.view-box:hover{
    filter: brightness(.7);
    -webkit-filter: brightness(.7);
}

.view-box:hover .play-button {
    display: block;
}
form {
    position: absolute;
    padding-top: 1rem;
    padding-left: 1rem;
}
.search-bar {
    height: 1rem;
    font-size: 1rem;
}

.search-button {
    width: 100px;
    height: 1.3rem;
    margin: 1rem;
    font-family: "josefin Sans", sans-serif;
}
.information {
    position: absolute;
    bottom: 0;
    background-color: rgba(0,0,0, .1);
    width: 100%;
}

.info-list {
    list-style: none;
}
.nav-wrap {
    background-color: rgba(0, 0, 0, .01);
    /* height: 90vh; */
    padding: 90px 10px;
    width: 70px;
    display: flex;
    justify-content: center;
}

.nav-wrap:hover {
    background-color: rgba(0, 0, 0, .3);
}

.icon {
    font-size: 3rem;
}
#wrapper {
    height: 100vh;
    width: 100%;
}

.slideshow {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.loading {
    position: fixed;
    left: 50%;
    top: 37%;
}

.loading .outer {
    border: 4px solid black;
    background-color: rgba(0, 0, 0, 0);
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    animation: load 2.7s ease-in-out 0s infinite alternate-reverse ;
    -webkit-animation: load 2.7s ease-in-out 0s infinite alternate-reverse ;
}

.loading .inner {
    background-color: rgba(199, 0, 0, 0);
    position: relative;
    /*left: 2rem;
    */top: 1.5rem;
    border: 4px solid rgba(2, 175, 255, .7);
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    animation: load 2.5s ease-in-out 0s infinite alternate ;
    -webkit-animation: load 2.5s ease-in-out 0s infinite alternate ;
}

@-webkit-keyframes load {
    0% {
        transform: scale(1);
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -ms-transform: scale(1);
        -o-transform: scale(1);
}

    100% {
        transform: scale(4);
        -webkit-transform: scale(4);
        -moz-transform: scale(4);
        -ms-transform: scale(4);
        -o-transform: scale(4);
}

    /* 100% {
        transform:  scale(1);
        -webkit-transform:  scale(1);
        -moz-transform:  scale(1);
        -ms-transform:  scale(1);
        -o-transform:  scale(1);
} */
}

@keyframes load {
    0% {
        transform: scale(1);
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -ms-transform: scale(1);
        -o-transform: scale(1);
}

    100% {
        transform: scale(4);
        -webkit-transform: scale(4);
        -moz-transform: scale(4);
        -ms-transform: scale(4);
        -o-transform: scale(4);
}

    /* 100% {
        transform:  scale(1);
        -webkit-transform:  scale(1);
        -moz-transform:  scale(1);
        -ms-transform:  scale(1);
        -o-transform:  scale(1);
} */
}
